<template>
  <li class="module-item">
    <h3 @click="isSee">{{ video.module }}</h3>
    <ul class="lession-list" v-if="see">
      <li v-for="leccion in video.lecciones" :key="leccion.id">
        <!-- <router-link :to="`${$route.path}/video/${leccion.id}?m=${video.id}`"> -->
        <router-link
          :to="{
            name: 'Video',
            params: { id: 1, idVideo: leccion.id },
            query: { m: 1 },
          }"
        >
          lession {{ leccion.id }}
        </router-link>
      </li>
    </ul>
  </li>
</template>

<script>
export default {
  props: ["video"],
  data() {
    return {
      see: false,
    };
  },
  methods: {
    isSee() {
      this.see = !this.see;
    },
  },
};
</script>

