<template>
  <div class="course-container">
    <div class="video-container">
      <iframe
        :src="videoPlayer"
        frameborder="0"
        allow="autoplay; fullscreen"
        allowfullscreen
        ref="video"
      ></iframe>
    </div>
    <div class="lession-container">
      <div class="class-info">
        <base-title color="--white">Leccion</base-title>
      </div>
      <div class="lession-list">
        <ul v-for="video in videos" :key="video.id">
          <h3 class="module-title">module {{ video.id }}</h3>
          <li
            v-for="leccion in video.lecciones"
            :key="leccion.id"
            class="lession-item"
          >
            <router-link :to="`/cursos/1/video/${leccion.id}?m=${video.id}`">
              Leccion {{ leccion.id }}
              <p>04:20</p>
            </router-link>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  // props: ["pageTitle"],
  data() {
    return {
      pageTitle: "",
      videos: [
        {
          module: "module 1",
          id: 1,
          lecciones: [
            {
              id: 1,
              name: "Leccion 1",
              link: "https://player.vimeo.com/video/495050193",
            },
            {
              id: 2,
              link: "https://www.youtube.com/embed/Mn7Bv8rGRzg",
            },
            {
              id: 3,
              link: "https://player.vimeo.com/video/495050193",
            },
          ],
        },
        {
          module: "module 2",
          id: 2,
          lecciones: [
            {
              id: 4,
              link: "https://player.vimeo.com/video/495050193",
            },
            {
              id: 5,
              link: "https://www.youtube.com/embed/Mn7Bv8rGRzg",
            },
            {
              id: 6,
              link: "https://player.vimeo.com/video/495050193",
            },
          ],
        },
        {
          module: "module 3",
          id: 3,
          lecciones: [
            {
              id: 7,
              link: "https://player.vimeo.com/video/495050193",
            },
            {
              id: 8,
              link: "https://www.youtube.com/embed/Mn7Bv8rGRzg",
            },
            {
              id: 9,
              link: "https://player.vimeo.com/video/495050193",
            },
          ],
        },
      ],
    };
  },
  watch: {
    $route(value) {
      if (value.params.idVideo) {
        const video = this.getVideo();
        document.title = `Leccion ${video.id}`;
      } else {
        this.$router.push({ name: value.name });
      }
    },
  },
  computed: {
    videoPlayer() {
      const video = this.getVideo();
      return video.link;
    },
    getTitle() {
      return this.getVideo().id;
    },
  },
  methods: {
    getVideo() {
      const module = Number(this.$route.query.m);
      const id = Number(this.$route.params.idVideo);
      const currentVideo = this.videos
        .find((video) => video.id === module)
        .lecciones.find((video) => video.id === id);
      return currentVideo;
    },
  },
  mounted() {
    document.title = "leccion " + this.getTitle;
  },
};
</script>

<style scoped>
.course-container {
  display: flex;
}
.video-container {
  width: 70%;
  height: 100vh;
}
iframe {
  width: 100%;
  height: 100%;
}
.lession-container {
  width: 30%;
  height: 100vh;
  /* padding: 5rem 4rem; */
  background: #063549;
  overflow-y: scroll;
}
.lession-container::-webkit-scrollbar {
  width: 0.5em;
}

.lession-container::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.2);
}

.lession-container::-webkit-scrollbar-thumb {
  background-color: #fefefe;
  border-radius: 10px;
  /*   outline: 1px solid green; */
}

.class-info {
  padding: 1rem 4rem;
}
ul {
  padding: 0;
  list-style: none;
}
.module-title {
  font-size: 3rem;
  font-weight: 500;
  padding: 0 4rem;
  margin: 0;
  background: #084661;
  color: white;
}
.lession-item a {
  display: flex;
  justify-content: space-between;
  text-decoration: none;
  align-items: center;
  padding: 0 4rem;
  font-size: 2rem;
  color: white;
  font-weight: 300;
}

.router-link-active,
.lession-item:hover {
  background: #fa7e42;
}

@media screen and (max-width: 823px) {
  .course-container {
    flex-wrap: wrap;
  }
  .video-container {
    width: 100%;
    height: 500px;
  }
  .lession-container {
    width: 100%;
  }
}
@media screen and (max-width: 768px) {
  .video-container {
    height: 250px;
  }
}
</style>